import React, { useState } from 'react';
import PropTypes from 'prop-types';

function loginUser(credentials) {
  return fetch('https://expense-e-tracker.herokuapp.com/login1', {
    method: "POST",
    body: JSON.stringify(credentials),
    headers: {
      "Content-Type": "application/json"
    },
    credentials: "same-origin"
  })
    .then(data => data.json())
    .then(response => { return response })
    .then(window.location.reload ())
    

    
}

export default function Login({ setToken }) {


  const [email, setEmail] = useState();
  const [password, setPassword] = useState();
  const [error, setError] = useState("");

  const handleSubmit = e => {
    e.preventDefault();
    const token = loginUser({
      email,
      password
    });

    if (token.email != null)
      setToken(token?.email);
    else {

      setError("Invalid UserName or Password")

    }

  }


  return (
    <div className="border border-info mt-2 pb-4 pt-5" style={{color:'white',background: 
    "linear-gradient(rgba(0,0,0,0.80), rgba(0,0,0,0.80)),url('https://images.pexels.com/photos/6328938/pexels-photo-6328938.jpeg?auto=compress')",
    height:'55vh',display: "flex", flexDirection: "column", alignItems: "center" ,backgroundSize:'cover',
    backgroundRepeat: 'no-repeat'}}>
      <h1>Please Login</h1>
      <form className="form-horizontal" onSubmit={handleSubmit}>


        <div className="form-group">
          <label className="control-label col-sm-2">
            <p>Email</p>
            
          </label>
          <input className="form-control" type="text" onChange={e => setEmail(e.target.value)} required />
        </div>

        <div className="form-group">
          <label className="control-label col-sm-2">
            <p>Password</p>
            
          </label>
          <input className="form-control" type="password" onChange={e => setPassword(e.target.value)} required/>
        </div>


        <div className="form-group mt-3 mx" >
          <button className="btn btn-success" type="submit">Submit</button>
        </div>
      </form>
      <div className="alert-danger">{error} </div>
    </div>
  )
}

Login.propTypes = {
  setToken: PropTypes.func.isRequired
};