import React from 'react';
import { Navbar, NavbarBrand, Nav, Collapse, NavItem, Jumbotron, Button } from 'reactstrap';
import { NavLink } from 'react-router-dom';

function Header(){

    
        return (
            <React.Fragment>
                <Navbar dark expand="md">
                    <div className="container">
                        <NavbarBrand className="mr-auto" href="/">
                            <img src="assets/images/logo.png" height="30" width="41"
                                alt="Ristorante Con Fusion" />
                        </NavbarBrand>
                        <Collapse navbar>
                            <Nav navbar>
                                <NavItem>
                                    <NavLink className="nav-link" to="/home">
                                        <span className="fa fa-home fa-lg"></span> Home
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/AllExpenses">
                                        <span className="fa fa-info fa-lg"></span> Expenses
                                    </NavLink>
                                </NavItem>
                                
                                <NavItem>
                                    <NavLink className="nav-link" to="/AddEdit">
                                        <span className="fa fa-address-card fa-lg"></span> Add
                                    </NavLink>
                                </NavItem>
                            </Nav>

                            <Nav className="ms-auto" navbar>
                                <NavItem>
                                    <NavLink to="/register"><Button><span className="fa fa-sign-in fa-lg"></span>Register</Button></NavLink>
                                </NavItem>
                            </Nav>
                        </Collapse>
                    </div>
                </Navbar>
                <Jumbotron>
                    <div className="container">
                        <div className="row row-header">
                            <div className="col-12 col-sm-12">
                                <h1>Expense Tracker</h1>
                                <h5>Expense Tracker is an easy solution to manage your messy expenses in hassle free manner.</h5>
                            </div>
                        </div>
                    </div>
                </Jumbotron>
            </React.Fragment>
        );
    
}

export default Header;