import React, {useState, useEffect} from 'react';
import { Breadcrumb, BreadcrumbItem, Table } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Loading } from './LoadingComponent';
import useToken from './useToken';
import Login from './Login';
import { baseUrl } from '../shared/baseUrl';

function handleDelete(ex) {
    if (window.confirm("Are you sure you want to delete this entry!!!")) {

        fetch(baseUrl + 'deleteExpense', {
            method: "POST",
            body: JSON.stringify(ex),
            headers: {
                "Content-Type": "application/json"
            },
            credentials: "same-origin"
        })
            .then(response => {
                if (response.ok) {
                    return response;
                } else {
                    var error = new Error('Error ' + response.status + ': ' + response.statusText);
                    error.response = response;
                    throw error;
                }
            },
                error => {
                    throw error;
                })
            .catch(error => { console.log('post comments', error.message); });

        window.location.reload();
    }
    else{

    }
}

function fetchExpenses(value) {

    return fetch('https://expense-e-tracker.herokuapp.com/getExpenses', {
        method: "POST",
        body: JSON.stringify(value),
        headers: {
            "Content-Type": "application/json"
        },
        credentials: "same-origin"
    })
        .then(response => {
            if (response.ok) {
                return response;
            } else {
                var error = new Error('Error ' + response.status + ': ' + response.statusText);
                error.response = response;
                throw error;
            }
        },
            error => {
                var errmess = new Error(error.message);
                throw errmess;
            })
        .then(response => response.json())
        .then(expense => expense)
        .catch(error => alert(error));
}

function Render(props) {

    const [expense, setExpense] = useState()
    const [sortBy, setSortBy] = useState("Date")
    const [sortType, setSortType] = useState("Desc")

    useEffect(() => {
        const data = {
            token: props.token,
            sortBy: sortBy,
            sortType: sortType
        }

        fetchExpenses(data).then(expense => setExpense(expense))

    }, [sortBy, sortType]);

    if (expense != null) {
        return (


            <div >
                <br/>
                
                <div className="d-flex d-flex justify-content-between" >
                <div  >
                <Link to={{ pathname: "/AddEdit"}}><button className="btn btn-light">ADD</button></Link>
                </div>

                <div  >
                    
                    <Link to={{ pathname: "/Breakdown"}}><button className="btn btn-light">Category Wise Distribution</button></Link>
                </div>

                <div  >
                    
                    <Link to={{ pathname: "/DateForm"}}><button className="btn btn-light">Specific Duration</button></Link>
                </div>
                    <div className="ml-auto">
                    <form className="form-horizontal">

                        <label className="m-2"> Sort By :</label>
                        <label className="m-2"><select class="form-control" value={sortBy} name="sortBy"
                            onChange={(e) => setSortBy(e.target.value)}>
                            <option>Date</option>
                            <option>Amount</option>
                        </select></label>
                        <label className="m-2"> Sort Type :</label>
                        <label className="m-2">
                            <select class="form-control" value={sortType} name="sortType"
                                onChange={(e) => setSortType(e.target.value)}>
                                <option>Desc</option>
                                <option>Asc</option>
                            </select>
                        </label>

                    </form>
                    </div>
                </div>
                <br />
                <Table style={{ color:'white' }} striped={true} bordered={true} className="border border-warning">
                    <thead>
                        <tr>
                            <th>NAME</th>
                            <th>DATE</th>
                            <th>AMOUNT</th>
                            <th>DETAILS</th>
                            <th>EDIT</th>
                            <th>DELETE</th>
                        </tr>
                    </thead>
                    <br />
                    {expense.map((ex) => {
                        return (

                            <tbody >
                                <tr >
                                    <td style={{ color:'white' }}>{ex.category}</td>
                                    <td style={{ color:'white' }}>{ex.date}</td>
                                    <td style={{ color:'white' }}>{ex.amount}</td>
                                    <td style={{ color:'white' }}>{ex.details}</td>
                                    <td ><Link to={{ pathname: "/AddEdit", state: { expense: ex } }}><button className="btn-info">edit</button></Link></td>
                                    <td><button className="btn-warning" onClick={() => handleDelete(ex)}>delete</button></td>

                                </tr>
                            </tbody>

                        );
                    })}
                </Table>

            </div>

        );
    }
    else {
        return (

            <div><Loading /> </div>

        );
    }



}

function Expenses(props) {



    const { token, setToken } = useToken();

    useEffect(() => { }, []);



    if (!token && token == null) {
        return <Login setToken={setToken} />
    }



    return (
        <div className="mt-1" style={{color:'white',background: 
        "linear-gradient(rgba(0,0,0,0.80), rgba(0,0,0,0.80)),url('https://images.pexels.com/photos/6328938/pexels-photo-6328938.jpeg?auto=compress')",
        backgroundSize:'cover',
        backgroundRepeat: 'no-repeat',
        }}>
            <div className="container">
            <div className="row">
                <Breadcrumb>
                    <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                    <BreadcrumbItem active>All Expenses</BreadcrumbItem>
                </Breadcrumb>

            </div>
            <div className="row row-content">
                <div className="col-12">
                    <h2>Your Complete Expenses</h2>
                </div>
                <Render token={token} />
            </div>
            </div>
        </div>
    );
}

export default Expenses;