import React from 'react';
import { Link } from 'react-router-dom';


function Home(props) {

    return (
        
        <div style={{
            background: 
            "linear-gradient(rgba(0,0,0,0.80), rgba(0,0,0,0.80)),url('https://images.pexels.com/photos/6328938/pexels-photo-6328938.jpeg?auto=compress')",
            height:'75vh',
            fontSize:'30px',
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat',
            
            
        }} className="mt-1">
           
            <div style={{color:'white',textShadow:"0 0 35px black" ,display: "flex",flexDirection: "column", justifyContent: "center",alignItems: "center"}} className="container pt-4">

                    <br/>
                    <p>ExpenseTracker lets you add and manage your daily expenses.</p>
                    <p>You can sort your expenditures by the dates</p>
                    <p>or by the amount that costed you the most.</p>
                    <br/>
                    <div style={{display: "flex",flexDirection: "column", justifyContent: "center",alignItems: "center"}}>
                    <Link  to={{ pathname: "/register"}}><button className="btn btn-warning">Add Your First Expense</button></Link>
                    <br/>
                    <Link to={{ pathname: "/AllExpenses"}}><button className="btn btn-warning">Continue Managing You Expenses</button></Link>
                    </div>
            </div>
        </div>
    );
}
export default Home;