import * as ActionTypes from './ActionTypes';


export function Expenses(state = {
  isLoading: true,
  errMess: null,
  expense: []
}, action) {
  switch (action.type) {
    case ActionTypes.ADD_EXPENSES:
      {
        
        return { ...state, isLoading: false, errMess: null, expense: action.payload };
      }

    case ActionTypes.EXPENSES_LOADING:
      return { ...state, isLoading: true, errMess: null, expense: [] };

    case ActionTypes.EXPENSES_FAILED:
      return { ...state, isLoading: false, errMess: action.payload };

    default:
      return state;
  }
}