import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import DatePicker from "react-datepicker";
import useToken from './useToken';
import Login from './Login';

export default function DateForm() {


  const [start, setStart] = useState();
  const [end, setEnd] = useState();
  const { token, setToken } = useToken();



  if (!token && token == null) {
      return <Login setToken={setToken} />
  }

  return (
    <div className="border border-info mt-2 pb-4 pt-5" style={{color:'white',background: 
    "linear-gradient(rgba(0,0,0,0.80), rgba(0,0,0,0.80)),url('https://images.pexels.com/photos/6328938/pexels-photo-6328938.jpeg?auto=compress')",
    height:'55vh',display: "flex", flexDirection: "column", alignItems: "center" ,backgroundSize:'cover',
    backgroundRepeat: 'no-repeat'}}>
      <h1>Select Dates</h1>
      <form className="form-horizontal" >


        <div className="form-group">
          <div>
          <label className="control-label ">
            <p>From Date :</p>
            
          </label>
          </div>
          <div>
          <DatePicker selected={start} onChange={(date) =>setStart(date)} dateFormat="yyyy-MM-dd"   />
          </div>
        </div>

        <div className="form-group">
          <label className="control-label ">
            <p>To Date :</p>
            
          </label>
          <DatePicker selected={end} onChange={(date) =>setEnd(date)} dateFormat="yyyy-MM-dd"   />
           </div>


        <div className="form-group mt-3 mx" >
        <Link to={{ pathname: "/SpecificPeriod", state: { start:start,end:end } }}><button className="btn-light">Submit</button></Link>
        </div>
      </form>
     
    </div>
  )
}

DateForm.propTypes = {
  setToken: PropTypes.func.isRequired
};