
import React, { Component, useState } from 'react';
import { useHistory } from "react-router";
import { baseUrl } from '../shared/baseUrl';
import useToken from './useToken';


function Register() {

    const [username, setUserName] = useState();
    const [email, setEmail] = useState();
    const [password, setPassword] = useState();
    
  const [emailError, setEmailError] = useState();
  const [passwordError, setPasswordError] = useState();
    const history = useHistory();

    function handleRegister(e){

      setEmailError("")
      setPasswordError("")

        if(!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email))
        {
            setEmailError("Invalid Email Address !!!")
        }
        else if(password.length<8)
        {
            setPasswordError("Password cannot be less than 8 Characters!!!")
        }
        else{
        
        e.preventDefault();
            const user = {
                name: username,
                email:email,
                password:password
            };

            fetch("https://expense-e-tracker.herokuapp.com/" + 'register1', {
                method: "POST",
                body: JSON.stringify(user),
                headers: {
                    "Content-Type": "application/json"
                },
                credentials: "same-origin"
            })
                .then(response => {
                    if (response.ok) {
                        return response;
                    } else {
                        var error = new Error('Error ' + response.status + ': ' + response.statusText);
                        error.response = response;
                        throw error;
                    }
                },
                    error => {
                        throw error;
                    })
                    .then(response=>response.json())
                .then(response => {if(response.status==="user found"){setEmailError("Email Id already exist")}
                                    else{history.push("/home");}})
                /*.then(response => { alert(JSON.stringify(response)); })*/
                .catch(error => { console.log('post comments', error.message); alert('Your comment could not be posted\nError: ' + error.message); });
    
    
            

                  }
    
    }



    const { token, setToken } = useToken();



    if (!token && token == null) {
    return(
      <div className="border border-info mt-2 pb-4 pt-5" style={{color:'white',background: 
      "linear-gradient(rgba(0,0,0,0.80), rgba(0,0,0,0.80)),url('https://images.pexels.com/photos/6328938/pexels-photo-6328938.jpeg?auto=compress')",
      height:'60vh',display: "flex", flexDirection: "column", alignItems: "center" ,backgroundSize:'cover',
      backgroundRepeat: 'no-repeat'}}>
          <h1>Register</h1>
          <form className="form-horizontal" onSubmit={handleRegister}>
            <div>
            <label>
              <p>Username :</p>
              <input type="text" onChange={e => setUserName(e.target.value)} required/>
            </label>
            </div>


            <div>
            <label>
              <p>Email :</p>
              <input type="text" onChange={e => setEmail(e.target.value)} required/>
            </label>
            </div>
            <div className="alert-danger">{emailError} </div>
            <br/>
            <div>
            <label>
              <p>Password :</p>
              <input type="password" onChange={e => setPassword(e.target.value)} required/>
            </label>
            </div>
            <div className="alert-danger">{passwordError} </div>
            

            


            <div className="mt-3">
              <button className="btn btn-info" type="submit">Submit</button>
            </div>
          </form>
        </div>
      )
    }
    else{
      return(
      <div className="border border-info mt-2 pb-4 pt-5" style={{color:'white',background: 
      "linear-gradient(rgba(0,0,0,0.80), rgba(0,0,0,0.80)),url('https://images.pexels.com/photos/6328938/pexels-photo-6328938.jpeg?auto=compress')",
      height:'60vh',display: "flex", flexDirection: "column", alignItems: "center" ,backgroundSize:'cover',
      backgroundRepeat: 'no-repeat'}}>
        <h3>You Are Already Logged In !!!</h3>
      </div>
      )
    }


    
}

export default Register;