import { render } from '@testing-library/react';
import React, { useState } from 'react';
import { useLocation } from 'react-router-dom';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css"; 
import { baseUrl } from '../shared/baseUrl';
import useToken from './useToken';
import Login from './Login';



function Form(props) {

    
    const { token, setToken } = useToken();
    
    const location = useLocation();
    

    var expense = ""

    if (location.state != null && location.state.expense != undefined)
    {
        
        expense = {
            id: location.state.expense.id,
            category: location.state.expense.category,
            amount: location.state.expense.amount,
            date: new Date(location.state.expense.date),
            details: location.state.expense.details,
            token: location.state.expense.token
        }
    }
    else
        expense = {
            id: '',
            category: 'Food',
            amount: '',
            date: new Date(),
            details: '',
            token: ''
        }


    
    const [category, setCategory] = useState(expense.category);

    const [amount, setAmount] = useState(expense.amount);

    const [date, setDate] = useState(expense.date);

    const [details, setDetails] = useState(expense.details);

   


    if (!token && token===null) {
        return <Login setToken={setToken} />
    }



    function saveValues(e) {
        e.preventDefault();
        const edited = {
            id: expense.id,
            category: category,
            amount: amount,
            date: date,
            details: details,
            token: token
        };
        fetch(baseUrl + 'saveExpense', {
            method: "POST",
            body: JSON.stringify(edited),
            headers: {
                "Content-Type": "application/json"
            },
            credentials: "same-origin"
        })
            .then(response => {
                if (response.ok) {
                    return response;
                } else {
                    var error = new Error('Error ' + response.status + ': ' + response.statusText);
                    error.response = response;
                    throw error;
                }
            },
                error => {
                    throw error;
                })
            .then(response => response.json())
            .catch(error => { console.log('post comments', error.message)});

            
            window.location="/#/AllExpenses";


    }

    render()
    {
        return (
            <div className="border border-info mt-2 pb-4 pt-5" style={{color:'white',background: 
    "linear-gradient(rgba(0,0,0,0.80), rgba(0,0,0,0.80)),url('https://images.pexels.com/photos/6328938/pexels-photo-6328938.jpeg?auto=compress')",
    height:'65vh',display: "flex", flexDirection: "column", alignItems: "center" ,backgroundSize:'cover',
    backgroundRepeat: 'no-repeat'}}>
                <form className="form-horizontal" onSubmit={saveValues}>
                    <div className="form-group">
                        <label className="control-label col-sm-2">category:
                        </label>
                        <select class="form-control" value={category} name="category"
                            onChange={(e) => setCategory(e.target.value)}>
                            <option>Food</option>
                            <option>Education</option>
                            <option>Health</option>
                            <option>Travel</option>
                            <option>Others...</option>
                        </select>
                    </div>
                    <br />
                    <div className="form-group">
                        <label className="control-label col-sm-2">
                            amount:

                        </label>
                        <input class="form-control" type="text" value={amount} name="amount"
                            onChange={(e) => setAmount(e.target.value)} required/>
                    </div>
                    <br />
                    <div className="form-group">
                        <label className="control-label col-sm-2">
                            date:

                        </label>
                        <DatePicker selected={date} onChange={(date) =>setDate(date)} dateFormat="yyyy-MM-dd"   />
                    </div>
                    <br />
                    <div className="form-group">
                        <label className="control-label col-sm-2">
                            description:

                        </label>
                    </div>
                    <div class="col-sm-10">
                        <input class="form-control" value={details} name="desc"
                            onChange={(e) => setDetails(e.target.value)} required/>
                    </div>
                    <br />

                    <button className='btn btn-warning'>Submit</button>
                </form>
            </div>


        );
    }
}


export default Form;