function Main()  {

  
  const { token, setToken } = useToken();

  function Header()
  {
    if (!token && token===null) {
      return(
        <Header1/>
      )
   }
   else
   {
    return(
      <Header2/>
    )
   }
  }
    return (

      <div >
         <Header/>
        <TransitionGroup>
          <CSSTransition  classNames="page" timeout={300}>
            <Switch >

              <Route path="/home" component={()=><Home/>}/>
              <Route path="/AddEdit" component={() => <Form/>} />
              <Route path="/AllExpenses" component={() => <Expenses />} />
              <Route path="/register" component={() => <Register />} />
              <Route path="/SpecificPeriod" component={() => <Specific />} />
              <Route path="/Breakdown" component={() => <Breakdown />} />
              <Route path="/DateForm" component={() => <DateForm/>} />
              
              <Redirect to="/home" />
            </Switch>
          </CSSTransition>
        </TransitionGroup>
        <Footer />
      </div>
    );
  
}

/*export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));*/
export default Main;