import React, {useState, useEffect} from 'react';
import { Breadcrumb, BreadcrumbItem, Table } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Loading } from './LoadingComponent';
import useToken from './useToken';
import Login from './Login';
import { useLocation } from 'react-router-dom';



function fetchExpenses(value) {

    return fetch('https://expense-e-tracker.herokuapp.com/specific', {
        method: "POST",
        body: JSON.stringify(value),
        headers: {
            "Content-Type": "application/json"
        },
        credentials: "same-origin"
    })
        .then(response => {
            if (response.ok) {
                return response;
            } else {
                var error = new Error('Error ' + response.status + ': ' + response.statusText);
                error.response = response;
                throw error;
            }
        },
            error => {
                var errmess = new Error(error.message);
                throw errmess;
            })
        .then(response => response.json())
        .then(expense => expense)
        .catch(error => alert(error));
}

function Render(props) {

    const [expense, setExpense] = useState()

    useEffect(() => {
        const data = {
            token: props.token,
            start: props.start,
            end: props.end
        }

        fetchExpenses(data).then(expense => setExpense(expense))

    }, []);

    if (expense != null) {
        var total=0;
        return (


            <div >
                <div className="d-flex justify-content-end " >
                <Link to={{ pathname: "/AllExpenses"}}><button className="btn btn-info">Back</button></Link>
                </div>
                <br />
                
               
                <Table style={{ color:'white' }} striped={true} bordered={true} className="border border-warning">
                    <thead>
                        <tr>
                            <th>CATEGORY</th>
                            <th>DATE</th>
                            <th>AMOUNT</th>
                            <th>DETAILS</th>
                        </tr>
                    </thead>
                    <br />
                    {expense.map((ex) => {
                        total=total+ex.amount;
                        return (

                            <tbody >
                                <tr >
                                    <td style={{ color:'white' }}>{ex.category}</td>
                                    <td style={{ color:'white' }}>{ex.date}</td>
                                    <td style={{ color:'white' }}>{ex.amount}</td>
                                    <td style={{ color:'white' }}>{ex.details}</td>
                                    

                                </tr>
                            </tbody>

                        );
                    })}
                </Table>
                <br/><br/>
                <div className="d-flex justify-content-end"><h4>Your total for this duration is : {total}</h4></div>
                
            </div>

        );
    }
    else {
        return (

            <div><Loading /> </div>

        );
    }



}

function Specific(props) {



    const { token, setToken } = useToken();

    useEffect(() => { }, []);
    const location = useLocation();



    if (!token && token == null) {
        return <Login setToken={setToken} />
    }


    

    if (location.state != null)
    {
    return (
        <div className="mt-1" style={{color:'white',background: 
        "linear-gradient(rgba(0,0,0,0.80), rgba(0,0,0,0.80)),url('https://images.pexels.com/photos/6328938/pexels-photo-6328938.jpeg?auto=compress')",
        backgroundSize:'cover',
        backgroundRepeat: 'no-repeat',
        }}>
            <div className="container">
            <div className="row">
                <Breadcrumb>
                    <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                    <BreadcrumbItem active>SpecificPeriod</BreadcrumbItem>
                </Breadcrumb>

            </div>
            <div className="row row-content">
                <div className="col-12">
                    <h2>dates</h2>
                </div>
                <Render token={token} start={location.state.start} end={location.state.end}/>
            </div>
            </div>
        </div>
    );
    }
}

export default Specific;